
.router-link-active {
  font-weight: 700;
}
button {
  background-color: #fff;
  color: #006505;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  padding: 0.5em;
  padding-left: 2em;
  padding-right: 2em;
  font-size: 1.3em;
  border-radius: 5px;
  transition: all 0.3s ease;
}
button:hover, button:focus {
  background-color: #006505;
  color: #fff;
}
button:hover.button-red, button:focus.button-red {
  background-color: #cc0000;
  color: #fff;
}
.link {
  margin: 1em;
  font-size: 1.3em;
  transition: all 0.3s ease;
}
.link:hover {
  color: #333;
  opacity: 0.5;
}
.content-title {
  color: #888;
  font-weight: 300;
  font-size: 2em;
  margin-bottom: 1em;
}
.navbar-nav {
  height: 80px;
}
li {
  height: 80px;
  padding-top: 15px;
}
.card-title {
  color: #555;
  font-size: 2em;
  font-weight: 300;
}
.card-content {
  padding: 1em;
}
td {
  padding: 0.5em;
}
tr.header,
td.header {
  font-weight: 700;
  background-color: #eee;
}
hr {
  background-color: #ddd;
  height: 1px;
  border: 0;
}
select {
  border: none;
  background: none;
}
input, textarea {
  border: none;
  padding: 0.8em;
  border-radius: 5px;
  outline: none;
  transition: all .3s ease;
}
input:focus, textarea:focus {
  box-shadow: 0 0 5px #999;
  background-color: #fcfcfc;
}



